body {
  background-color: #FAFAFA;
}

.message-list {
  padding: 10%;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
}

.message {
  -ms-flex-item-align: start;
      align-self: flex-start;
  display: inline-block;
  padding: 0 .8em 0 .8em;
  background: #fff;
  -webkit-animation: fade-in 1s;
          animation: fade-in 1s;
  margin: 1em;
  border-radius: 1em;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

@keyframes fade-in {
  from {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

.hero {
  -ms-flex-item-align: end;
      align-self: flex-end;
  float: left;
  color: #fff;
  background-color: #5e74ff;
}


